import APIService from "./index";
import config from "../common/config";
import jToken from "../common/jToken"

export const CatalogAPI = {

    getAllCategories: () => {
        return APIService.protectPostWithCaching("/catalog/getAllCategories", {}, 3600)
    },

    getProductsByCategory: (payload) => {
        let {categoryId, subCategoryId, itemsPerPage} = payload;

        if (subCategoryId) {
            return APIService.protectPostWithCaching("/catalog/getProductsByCategory", {
                params: {
                    categoryId,
                    subCategoryId,
                    itemsPerPage
                },

                keys: {
                    categoryId,
                    subCategoryId,
                },
            }, 3600)
        } else {
            return APIService.protectPostWithCaching("/catalog/getProductsByCategory", {
                params: {
                    categoryId,
                    itemsPerPage
                },

                keys: {
                    categoryId,
                },
            }, 3600)
        }
    },

    getMoreProductsByCategory: (payload) => {
        let {categoryId, subCategoryId, pageNumber, itemsPerPage} = payload;

        if (subCategoryId) {
            return APIService.protectPostWithCaching("/catalog/getMoreProductsByCategory",
                {
                    params: {
                        categoryId,
                        subCategoryId,
                        pageNumber,
                        itemsPerPage
                    },

                    keys: {
                        categoryId,
                        subCategoryId,
                        pageNumber,
                    },

                }, 3600)
        } else {
            return APIService.protectPostWithCaching("/catalog/getMoreProductsByCategory", {
                params: {
                    categoryId,
                    pageNumber,
                    itemsPerPage
                },

                keys: {
                    categoryId,
                    pageNumber,
                },
            }, 3600)
        }
    },

    getSearchHelper: (request) => {
        return APIService.protectedPost("/catalog/searchHelper", {request})
    },

    search: (request) => {
        return APIService.protectPostWithCaching("/catalog/search", {request}, 60)
    },

    createProductsCountInStockUpdater() {
        return {
            socket: new WebSocket(config.WS_API_URL() + "/catalog/countInStock?token=" + jToken.get()),

            sendJSONMessage(message, callback) {
                this.socket.send(JSON.stringify(message));
                if (callback) {
                    this.socket.onmessage = callback;
                }
            }
        }
    },

    createOrder: (comment, products) => {
        return APIService.protectedPost("/catalog/createOrder", {comment, products})
    },
};